.profile {
    margin-top: 95px;

    &__img {
        max-width: 90px;
        display: block;
        margin: 0 auto 20px;
    }

    .form-control {
        background-color: #f5f5f5;
        border: none;
        padding: 1rem;
        font-size: 1rem;
        color: $black;
        box-shadow: 0 0.4rem #dfd9d9;
    }

    .form-label {
        color: $black;
    }
}