.catalog {
    margin-top: 95px;
    padding-top: 40px;

    &__title {
        color: $black;
    }

    &__filter {
        background-color: $dark-blue;
        padding: 10px;
        display: flex;
        align-items: center;
        gap: 20px;
        margin-bottom: 30px;
    }

    &__slider {
        height: 600px;
        position: relative;
        margin-bottom: 30px;
    }

    .swiper-button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 2;

        &:not(.-prev) {
            right: 10px;
        }

        &.-prev {
            left: 10px;
        }
    }
}