.form {
    width: 400px;
    padding: 20px;
    border-radius: 20px;
    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 15px );
    -webkit-backdrop-filter: blur( 15px );
    border: 1px solid rgba( 255, 255, 255, 0.18 );

    &__wrapper {
        position: relative;
        height: 100vh;
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        background: rgb(41,46,73);
        background: linear-gradient(180deg, rgba(41,46,73,1) 50%, rgba(83,105,118,1) 100%);
    }

    &__link {
        display: block;
        width: 100%;
        text-align: center;
        color: $white;
        opacity: .7;
        font-size: 14px;
        margin-top: 30px;

        &:hover {
            text-decoration: underline;
        }
    }

    &__logo {
        position: absolute;
        left: 50%;
        top: 40px;
        transform: translateX(-50%);
    }

    &__title {
        margin-top: 0;
        font-size: 30px;
        color: $white;
        margin-bottom: 15px;
        opacity: .9;
        text-align: center;
    }

    &-group {
        position: relative;

        &.error {
            .form-label {
                color: $red;
                opacity: 1;
            }
            
            svg {
                color: $red;
                opacity: 1;
            }

            .form-control {
                border-color: $red;
                opacity: 1;
            }
        }

        &.success {
            .form-label {
                color: $green;
                opacity: 1;
            }
            
            svg {
                color: $green;
                opacity: 1;
            }

            .form-control {
                border-color: $green;
                opacity: 1;
            }
        }

        &:not(:last-of-type) {
            margin-bottom: 15px;
        }

        svg {
            margin-left: 5px;
            width: 25px;
            height: 25px;
            color: $white;
            opacity: .7;
        }

        &.btn-group {
            padding-top: 20px;
            display: flex;
            justify-content: center;

            button {
                width: 60%;
            }
        }
    }

    &-control {
        border: 0;
        border-radius: 0;
        background: none;
        outline: none;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);

        width: 100%;
        padding: 10px;
        padding-left: $input-padding-x;
        color: $white;
        font-size: 14px;

        &::placeholder {
            font-size: 0;
        }

        &:not(:placeholder-shown) + .form-label,
        &:focus + .form-label {
            top: -15px;
            font-size: 11px;
            opacity: 1;
        }

        &:hover,
        &:focus {
            color: $white;
            background: none;
            outline: none;
            box-shadow: none;
            border-color: $white;
        }
    }

    &-label {
        font-size: 14px;
        color: $white;
        position: absolute;
        left: $input-padding-x;
        top: 10px;
        opacity: .7;
        margin-bottom: 0;
        cursor: text;

        transition: all 150ms linear;
    }

    &-text-help {
        color: $red;
        margin-top: 5px;
    }
}