.product-card {
    &__top {
        display: block;
        padding: 10px;
        background-color: #f5f5f5;
        margin-bottom: 20px;
    }

    &__picture {
        max-width: 90%;
        margin: 0 auto;
        display: block;
    }

    &__info {
        padding: 0 10px;
    }

    &__name {
        font-size: 18px;
        color: $dark-blue;
        margin-bottom: 5px;
    }

    &__price {
        font-size: 16px;
        color: $dark-blue;
    }

    &__buttons {
        margin-top: 10px;
        background-color: $dark-blue;
        padding: 10px;

        button {
            width: 100%;
        }
    }
}