.btn {
    font-size: 12px;
    font-weight: 400;
    padding: 10px 15px;
    outline: none;
    border: none;
    border-radius: 0 !important;

    cursor: pointer;

    &-primary {
        position: relative;
        z-index: 1;

        color: $white;
        background-color: transparent;

        transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);

        a {
            transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);
        }

        &::before, &::after {
            content: '';
            position: absolute;	
            transition: inherit;
            left: 0;
            z-index: -1;
            height: 0;
            width: 100%;
        }

        &::before {
            bottom: 0;	
            border: 1px solid $white;
            border-top: 0;
            border-bottom: 0;
        }

        &::after {
            top: 0;
	        height: 0;
            border-right: 1px solid $white;
        }

        &:hover,
        &:focus {
            color: $dark-blue;
            transition-delay: .5s;

            a {
                color: $dark-blue !important;
            }

            &::before, &::after {
                height: 100%;
            }

            &::after {
                background-color: $white;
                transition-delay: .25s;
            }

            &::before {
                transition-delay: 0s;
            }
        }
    }

    &-outline {
        font-size: 12px;
        color: $white;
        padding: 10px 20px;
        background-color: transparent;
        opacity: .7;
        border: none;
        
        position: relative;

        transition: all .3s;

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            opacity: 0;
            transition: all 0.3s;
            border-top: 1px solid rgba(255,255,255,0.5);
            border-bottom: 1px solid rgba(255,255,255,0.5);
            transform: scale(0.1, 1);
        }

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            transition: all 0.3s;
            background-color: rgba(255,255,255,0.1);
        }

        &:hover {
            letter-spacing: 2px;
            opacity: 1;
            color: $white;

            &::before {
                opacity: 1; 
                transform: scale(1, 1);
            }

            &::after {
                opacity: 0; 
                transform: scale(0.1, 1);
            }
        }
    }

    &-circle {
        color: $white;
        background-color: $black;
        border: none;
        position: relative;
        overflow: hidden;

        span:not(:nth-child(6)) {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            height: 33px;
            width: 33px;
            background-color: #28a745;
            border-radius: 50%;
            transition: .6s ease;
        }
            
        span:nth-child(6) {
            position: relative;
            color: $white;
        }
            
        span:nth-child(1) {
            transform: translate(-3.3em, -4.5em);
        }
            
        span:nth-child(2) {
            transform: translate(-6em, 1.6em);
        }
            
        span:nth-child(3) {
            transform: translate(-.2em, 1.8em);
        }
            
        span:nth-child(4) {
            transform: translate(3.5em, 1.8em);
        }
            
        span:nth-child(5) {
            transform: translate(3.5em, -4.6em);
        }
            
        &:hover {
            span:not(:nth-child(6)) {
                transform: translate(-50%, -50%) scale(4);
                transition: 1.5s ease;
            }
        }
    }
}