.swiper-button {
    cursor: pointer;
    border: 0;
    background-color: transparent;
    outline: none;
    padding: 0;
    color: $black;

    svg {
        width: 40px;
        height: 40px;
        display: block;
        color: $dark-blue;
        transition: color .2s linear;
    }

    &.-prev {
        margin-right: 10px;

        svg {
            transform: rotate(180deg);
        }
    }

    &:hover {
        svg {
            color: $red;
        }
    }

    &:focus {
        outline: none !important;
        border: 0;
    }
}