.subscribe {
    background-color: $dark-blue;
    padding: 60px 0;

    &__title {
        color: $white;
        margin-bottom: 20px;
        text-align: center;
    }

    &__description {
        text-align: center;
        max-width: 100%;
    }

    &__form {
        margin: 40px auto 0;

        .form-group:last-of-type {
            margin-top: 40px;
        }
    }
}