.footer {
    background-color: $black;
    padding: 60px 0;

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__logo {
        display: block;
        width: 250px;

        img {
            width: 100%;
            height: auto;
        }
    }

    &__description {
        color: $white;
        margin-top: 40px;
        font-size: 12px;
        text-align: center;
    }
}