.notify{
    $this: &;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    text-align: center;

    @media (min-width: 576px) {
        width: 380px;
    }
    
    &__status{
        position: relative;
        width: 100px;
        height: 100px;
        margin-top: -86px; //Popup padding + 50% of status height

        .circle{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            padding: 2.5px;
            background-clip: content-box;
            animation: spin 20s linear infinite;
            background-color: #01af1d;
            border: 2.5px dashed #01af1d;

            @keyframes spin {
                100% {
                transform: rotateZ(360deg);
                }
            }
        }
        .icon{
            position: absolute;
            top: calc(50% - 55px / 2); //icon size
            left: calc(50% - 55px / 2);
            color: #fff;
            animation: rotateInRightLeft 1s ease-out;

            svg {
                width: 55px;
                height: 55px;
                color: $white;
            }

            @keyframes rotateInRightLeft {
                0%{
                    transform: rotate(0);
                }
                33%{
                    transform: rotate(25deg);
                }
                66% {
                    transform: rotate(-25deg);
                }
                100% {
                    transform: rotate(0);
                }
            }
        }
    }
    &__heading{
        margin-top: 18px;
        font-size: 24px;
        font-weight: 700;
        color: #01af1d;
    }
    &__message{
        margin-top: 18px;
        font-size: 16px;
    }
    &__button{
        background-color: #01af1d;
        width: 80%;
        margin-top: 36px;
        border-radius: 36px;
        font-weight: 400;
        line-height: 1;
        text-transform: initial;
        color: $white;
        animation: fadeTranslate 1s ease-out;
        @keyframes fadeTranslate {
            from{
            opacity: 0;
            transform: translateY(36px); //Popup padding
            }
            to{
            opacity: 1;
            transform: translateY(0);
            }
        }
    }
  }